
import AvatarIcon from "@svgicon/avatar_icon.vue";

export default {
  name: "avatar",

  props: {
    superiorAvatarFilePath: {
      type: String,
      required: false,
      default: "",
    },
  },

  inject: {
    avatarFilePath: {
      default: "",
    },
  },

  components: {
    AvatarIcon,
  },

  computed: {
    processedAvatarFilePath(): string {
      return this.superiorAvatarFilePath || this.avatarFilePath;
    },
  },
};
