
import Translator from "@jsmodules/translator";
import Avatar from '@jscomponents/user/avatar.vue';

export default {
  props: {
    userNickname: {
      type: String,
      required: true,
    },
  
    authenticatedUser: {
      type: Boolean,
      required: false,
      default: false,
    },
  
    avatarFilePath: {
      type: String,
      required: false,
      default: "",
    }
  },
  
  components : {
    Avatar,
  },
  
  data() {
    return {
      translations: Translator
    }
  },
  
  computed: {
    userLabel(): string {
  return this.authenticatedUser
    ? this.userNickname
    : `${this.translations.translate("unregistered_user")} - ${this.userNickname}`;
  }
  }
}

